*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

a,
a:link,
a:visited {
  text-decoration: none;
  outline: 0;
  border: 0;
}

ul,
li,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

form,
input,
select,
textarea,
section,
article,
span,
p {
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
}

:focus,
:visited,
:active {
  outline: 0;
  border: 0;
}

:root {
  --bg-color: #FAFCFD;
  --whiteColor: #FFFFFF;
  --blueColor: #151926;
  --grayColor: #DFE8ED;
  --darkGray: #8E97B6;
  --darkBlue: #141A31;
  --headerText: #8E97B6;
  --apiHoverColor: #f8f8f8;
  --API-docs: #FFFFFF;
}

.darkmode {
  --bg-color: #232737;
  --whiteColor: #1F2333;
  --blueColor: #FFFFFF;
  --grayColor: #D1D5E5;
  --darkGray: #8E97B6;
  --darkBlue: #5957CE;
  --headerText: #5957CE;
  --apiHoverColor: #2E3348;
  --API-docs: #FFFFFF;
}

body {
  font-family: "IBM Plex Sans Condensed", sans-serif;
  font-size: 16px;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "IBM Plex Sans Condensed", sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-weight: 600;
}

h1 {
  font-size: 1.75rem;
}

h2 {
  font-size: 1.375rem;
}

body {
  background-color: var(--bg-color);
  color: var(--blueColor);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.api-content {
  padding: 0 20px;
  margin-bottom: 100px;
}
.api-content .api-list, .api-content .bookmark-list {
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}
@media (max-width: 910px) {
  .api-content .api-list, .api-content .bookmark-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 572px) {
  .api-content .api-list, .api-content .bookmark-list {
    grid-template-columns: repeat(1, 1fr);
  }
}

.mb-40 {
  margin-bottom: 40px;
}

.mt-40 {
  margin-top: 40px;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 6.25rem;
}
header h1 {
  margin-bottom: 3.125rem;
  font-weight: 700;
  line-height: 2.275rem;
  text-align: center;
}

.navbar {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}
.navbar nav ul {
  display: flex;
}
.navbar nav ul li {
  margin-right: 40px;
  font-size: 22px;
  font-weight: 600;
}
.navbar nav ul li a {
  color: var(--bl);
  transition: 300ms color;
}
.navbar nav ul li a:hover {
  color: var(--headerText);
}
.navbar .dark-mode {
  font-size: 22px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 300ms color;
}
.navbar .dark-mode svg {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}
.navbar .dark-mode:hover {
  color: var(--headerText);
}

.api-search-input {
  width: 680px;
  max-width: 100%;
  position: relative;
  width: 719px;
  padding: 0 15px;
  box-shadow: 0 20px 53px -30px rgba(var(--grayColor), 0.57);
}
.api-search-input .search-icon {
  position: absolute;
  top: 0;
  left: 20px;
  width: 70px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.api-search-input .search-icon svg {
  width: 34px;
  height: 34px;
}
.api-search-input .search-icon svg path {
  stroke: var(--darkGray);
}
.api-search-input .api-input {
  font-size: 1.125rem;
  padding: 28px 40px 28px 89px;
  width: 100%;
  border-radius: 39.5px;
  border: 1px solid var(--darkGray);
  background-color: var(--whiteColor);
  color: var(--blueColor);
  font-weight: 400;
}
.api-search-input .api-input::placeholder {
  color: var(--darkGray);
}

.api-info {
  padding: 30px;
  background-color: var(--whiteColor);
  border-radius: 10px;
  display: flex;
  position: relative;
  box-shadow: 0 20px 53px -30px rgba(var(--grayColor), 0.57);
  min-height: 209px;
  overflow: hidden;
}
.api-info .api-icon {
  width: 61px;
  height: 61px;
  margin-right: 25px;
}
.api-info .api-icon img {
  object-fit: cover;
  border-radius: 50%;
  justify-content: flex-start;
}
.api-info .api-detail .api-title h2 {
  font-size: 22px;
  font-weight: 700;
  line-height: 28.6px;
  margin-bottom: 5px;
}
.api-info .api-detail .category {
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
  margin-bottom: 10px;
}
.api-info .api-detail .api-description {
  font-size: 16px;
  font-weight: 400;
  line-height: 20.8px;
  transition: 300ms opacity;
}
.api-info .api-hover {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background: var(--apiHoverColor);
  display: flex;
  align-items: center;
  transform: translateY(100%);
  transition: 300ms transform;
}
.api-info .api-hover a, .api-info .api-hover button {
  flex: 1;
  margin: 0 15px;
  padding: 10px 23px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 400;
}
.api-info .api-hover a {
  background-color: var(--darkBlue);
  color: var(--API-docs);
}
.api-info .api-hover a svg {
  width: 24px;
  height: 24px;
  margin-left: 7px;
}
.api-info .api-hover button {
  background-color: var(--whiteColor);
  color: var(--blueColor);
  cursor: pointer;
}
.api-info .api-hover button svg {
  width: 22px;
  height: 22px;
  margin-right: 3px;
}
.api-info:hover .api-detail .api-description {
  opacity: 0;
}
.api-info:hover .api-hover {
  transform: translateY(0);
}

.alert {
  padding: 20px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
}
.alert-warning {
  background-color: rgba(255, 171, 0, 0.2);
  color: #c28200;
  border: 1px solid #eb9d00;
}
.alert-primary {
  background-color: rgba(0, 144, 231, 0.2);
  color: #006db0;
  border: 1px solid #0084d5;
}
.alert-danger {
  background-color: rgba(252, 66, 74, 0.2);
  color: #c03238;
  border: 1px solid #e83d44;
}
.alert-info {
  background-color: rgba(143, 95, 232, 0.2);
  color: #6d48b0;
  border: 1px solid #8457d5;
}
.alert-success {
  background-color: rgba(0, 210, 91, 0.2);
  color: #00a045;
  border: 1px solid #00c154;
}
.alert-light {
  background-color: rgba(255, 255, 255, 0.2);
  color: #c2c2c2;
  border: 1px solid #ebebeb;
}